import "./blog.scss";
import { useState } from "react";

export default function Blog() {
    const [currentSlide, setCurrentSlide] = useState(0);
    const data = [
        {
            id: "1",
            icon: "./assets/med.png",
            title: "The legend of Spikey Boy™ — Creating a fully functioning 2D platformer using Unity",
            desc: "These are the main phases that our game needs to become a fully functioning game. And also just a reminder we are not going over to the previous blog, so if you miss it here is the link of that blog for recap so you can go along in this project. Also, this blog would be a lengthy one, so I hope you can stick along with me for the entire tutorial.",
            link: "https://csean.medium.com/the-legend-of-spikey-boy-creating-a-fully-functioning-2d-platformer-using-unity-aa0aeb8bd49d",
            img1: "./assets/blog2.png",
            img2: "./assets/blogimage2.png"
        },
        {
            id: "2",
            icon: "./assets/med.png",
            title: "Introduction to Unity",
            link: "https://csean.medium.com/introduction-to-unity-c512e9b6d4f7",
            desc: "Today we will go through all the basic stuff that you need to learn about developing a game in Unity using C# as the language. The focus of this tutorial is to introduce you to Unity and the foundation needed when creating an actual game — i.e. character creation, game physics, and animations.",
            img1: "./assets/blog1.png",
            img2: "./assets/blog1image.png"
        },
        {
            id: "3",
            icon: "./assets/med.png",
            title: "Comming Soon...",
            link: "",
            desc: "",
            img1: "",
            img2: ""
        }
    ];

    const handleClick = (way) => {
        way === "left"
          ? setCurrentSlide(currentSlide > 0 ? currentSlide - 1 : 2)
          : setCurrentSlide(currentSlide < data.length - 1 ? currentSlide + 1 : 0);
      };

    return (
        <div className="blog" id="blog"><h4>Blogs</h4>
            <div className="slider" style={{transform:`translateX(-${currentSlide * 100}vw)`}}>
                {data.map((d)=>(
                <div className="container">
                    <div className="item">
                        <div className="left">
                            <div className="leftcontainer">
                                <div className="imgcontainer">
                                    <img src="./assets/med.png" alt="test"/>
                                </div>
                                <h2>{d.title}</h2>
                                <p>
                                {d.desc}
                                </p>
                                <span><a href={d.link}>Link</a></span>
                            </div>
                        </div>
                        <div className="right">
                            <img src={d.img1} alt="" />
                            <img src={d.img2} alt=""/>
                        </div> 
                    </div>
                </div>))} 
            </div>
            <img src="./assets/arrow.png" alt="" className="arrow left" onClick={() => handleClick("left")}/>
            <img src="./assets/arrow.png" alt="" className="arrow right" onClick={() => handleClick()}/>
        </div>
    )
}
