import "./intro.scss"
import { init } from 'ityped'
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import { useEffect, useRef } from "react";

export default function Intro() {

    const textRef = useRef();

    useEffect(() => {
        init(textRef.current, {
            backDelay:1500,
            backSpeed: 60,
            showCursor: true,
            strings: ["Java", "JavaScript", "Unity","React","C#", "Spring Boot", "CSS/SCSS", "SQL", "Database Management"]
        });
    }, [])
    
    return (
        <div className="intro" id="intro">
            <div className="left">
                <div className="imgContainer">
                    <img src="./assets/me1.jpg" alt="intro_image"/>
                </div>
            </div>
            <div className="right">
                <div className="wrapper">
                    <h2>Hi there, I'm</h2>
                    <h1>Sean Cano</h1>
                    <h3>Experienced in <span ref={textRef}></span></h3>
                </div>
                <a href="#portfolio">
                    <KeyboardArrowDownIcon className="down"/>
                </a>
            </div>
        </div>
    )
}
